import React, { ReactElement } from "react";
import { BaseEditor } from "slate";
import { HistoryEditor } from "slate-history";
import { Editable, ReactEditor, RenderElementProps, Slate } from "slate-react";

declare module "slate" {
  interface CustomTypes {
    Editor: EditorType;
    Element: CustomElement;
    Text: CustomText;
    Node: CustomElement | EditorType | CustomText;
  }
}

export enum CET {
  EDITOR = "editor",
  NUMBER_LIST = "ol",
  NORMAL_LIST = "ul",
  LIST_ITEM = "li",
  DIV = "div",
  Block = "block",
  P = "p",
  H1 = "h1",
  H2 = "h2",
  H3 = "h3",
  H4 = "h4",
  IMG = "img",
  LINK = "link",
  TABLE = "table",
  TBODY = "tbody",
  TR = "tr",
  TD = "td",
  TODOLIST = "todo",
  CHECKBOX = "checkbox",
  CODE = "code",
  FILE = "file",
}

export const TextWrappers = [CET.DIV, CET.H1, CET.H2, CET.H3, CET.H4, CET.P];
export const InLineTypes = [CET.IMG, CET.LINK, CET.CHECKBOX];

export enum Marks {
  BOLD = "bold",
  ITALIC = "italic",
  FontSize = "fontSize",
  Underline = "underline",
  LineThrough = "line-through",
  Color = "color",
  BGColor = "backgroundColor",
  TextAlign = "textAlign",
}

export type CustomElement = {
  type: CET;
  [key: string]: any;
  url?: string; // Link
  fileName?: string; // 
  id?: number; // 
  border?: boolean; // img
  colSpan?: number; // td
  rowSpan?: number; // td 
  tdAutoWidth?: boolean; // 
  width?: number; // td
  height?: number; // td
  selected?: boolean; // td
  childrenWrapper?: "div" | "h1" | "h2" | "h3" | "h4"; // todo 
  // canTdEdit?: boolean; // td
  tdIsEditing?: boolean; // td
  checked?: boolean; // todoList
  openPopModal?: boolean; // 
  wrapperWidthWhenCreated?: number; // tabletd
  defaultMode?: string; // code 
  defaultCode?: string; // code 
  textAlign?: TextAlignEnum; // textWrappertodoListComp
  tdMap?: any; // tdMap
  children: (CustomText | CustomElement)[];
};
export type CustomText = { text: string; [key: string]: any } & Partial<{
  [key in Marks]: any;
}>;
export type EditorType = BaseEditor &
  ReactEditor &
  HistoryEditor & {
    setFixLayoutBox?(
      attr: {
        visible: boolean;
        left?: number;
        top?: number;
      },
      children?: ReactElement<any>
    ): void; // 
  };

export type StateShape = Parameters<typeof Slate>[0]["value"];
